body {
    background-color: black;
  }
  form {
    margin: 15px;
    color: white;
    max-width: 644px;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    border-radius: 5px;
  }
  
  nav a {
    text-decoration: none;
    color: red;
    font-weight: bold;
    font-size: 45px;
  }
  .header h1 {
    font-size: 30px;
    margin-bottom: 0px;
    margin-top: 0px;
  }
  
  /*input box*/
  .input input {
    width: 100%;
    margin-top: 30px;
    background-color: #333333;
    color: #8c8c8c;
    border: none;
    font-size: 15px;
    text-align: bottom;
    box-sizing: border-box;
    height: 50px;
    border-radius: 5px;
    padding: 20px;
    margin-top: 25px;
  }
  .input::placeholder {
    font-weight: 500;
  }
  
    input:focus {
    border-color: red;
  }
  /*button*/
  .signin button {
    width: 100%;
    font-size: 18px;
    color: white;
    border-radius: 5px;
    padding: 15px 0px;
    margin-top: 40px;
    background-color: #e50914;
    border: none;
    cursor: pointer;
  }
  /*footer content*/
  .form-check{
      display: flex;
      justify-content: space-between;
  }
  .form-check {
    margin: 10px;
    color: #8c8c8c;
  }
  
  .form-check a {
    text-decoration: none;
    color: #8c8c8c;
  }
  .new {
    margin: 15px;
    margin-top: 50px;
  }
  .new a {
    text-decoration: none;
    color: white;
    font-size: 20px;
  }
  .new p {
    color: gray;
    font-size: 16px;
  }
  
  .footer {
    color: gray;
    margin: 15px;
    font-size: 14px;
  }
  .footer a {
    text-decoration: none;
    color: blue;
  }
  .logo a{
    padding: 25px;
  }
  .error{
    color: #e50914;
  }
  @media all and (min-width: 740px) {
    body {
      background: linear-gradient(0deg, rgb(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
        url("../images/bg.jpg");
    }
    form {
      margin: auto;
      width: 330px;
      background-color: rgba(0, 0, 0, 0.8);
      padding: 70px;
    }
    .footer {
      font-size: 13px;
      margin-top: 2px;
    }
  }
  